/*
* Style Works for Editor Only
*/


.aab-color-popover {
  .components-color-picker {
    overflow: hidden;
  }
}

.aab-color-picker {
  margin: 5px 0;

  .aab-reset-btn {
    min-width: inherit;

    &.disabled {
      cursor: no-drop;

      .dashicon {
        color: #e0e0e0;
      }
    }

    &.active {
      .dashicon {
        color: var(--wp-admin-theme-color);
      }
    }

    .dashicon {
      align-items: center;
      box-sizing: initial;
      display: inline-flex;
      justify-content: center;
      padding: 2px;
      font-size: 12px;
      line-height: 20px;
    }
  }

  .components-base-control__label {
    margin-bottom: 0;
    font-size: 13px;
    text-transform: capitalize;
    font-weight: 400;
  }

  .components-button {
    padding: 0;
  }

  .component-color-indicator {
    height: 25px;
    width: 25px;
    background: linear-gradient(45deg, #ddd 25%, transparent 0), linear-gradient(-45deg, #ddd 25%, transparent 0), linear-gradient(45deg, transparent 75%, #ddd 0), linear-gradient(-45deg, transparent 75%, #ddd 0);
    background-position: 0 0, 0 5px, 5px -5px, -5px 0;
    background-size: 10px 10px;
  }
}

.components-border-box-control{
  .components-number-control.components-unit-control{
    margin-bottom: 0;
  }
}

.wp-block-aab-group-accordion {
  border: 1px dashed #cccc;
  padding: 15px 15px 30px 15px !important;




  .wp-block-aab-accordion-item {
    .block-list-appender {
      left: auto !important;
      right: 0;
      margin-left: 0 !important;
    }
  }

  .block-list-appender {
    left: 50% !important;
    right: auto;
    margin-left: -12px !important;
  }

  [aria-label="Add Single Accordion"] {
    border-radius: 50% !important;
    position: absolute;
    bottom: -35px !important;
  }
}

.wp-block-post-content {
  .ezd-form-group {
    input {
      width: 100%;
    }
  }
}

.aab-pro-element.is-opened {
  background: gainsboro;
  cursor: pointer;
  position: relative;
  color: #0000004f;

  &:after {
    content: "PRO";
    clear: both;
    background: rgb(0 0 0 / 42%);
    position: absolute;
    top: 48px;
    right: 3px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    color: white;
    z-index: 99999;
    left: 4px;
    bottom: 4px;
    width: -webkit-fill-available;
    border-radius: 3px;
  }
}

.block-editor-block-styles__variants {
  button {
    &[aria-label="aab-style-pro-checked"] {
      display: none !important;
    }
  }
}

.custom-css-notice {
  font-style: italic !important;
}